import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { ProductService } from 'src/app/service/product.service';
import { DropDownService } from 'src/app/service/drop-down.service';
import { Location } from '@angular/common';
declare var swal: any;

@Component({
  selector: 'app-product-add',
  templateUrl: './product-add.component.html'
})
export class ProductAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;
  showLoader = false;
  formErrors = {
    name: null,
    apierror: null
  };
  isEditing = false;
  editId = null;
  brandList: Array<any> = [];
  categoryList: Array<any> = [];
  doc = {
    type: "doc",
    content: [
      {
        type: "heading",
        attrs: {
          level: 3
        },
        content: [
          {
            type: "text",
            text: "Hello"
          }
        ]
      },
      {
        type: "paragraph",
        content: [
          {
            type: "text",
            text: "This is editable text. You can focus it and start typing."
          }
        ]
      },
      {
        type: "heading",
        attrs: {
          level: 3
        },
        content: [
          {
            type: "text",
            text: "The code block is a code editor"
          }
        ]
      },
      {
        type: "paragraph",
        content: [
          {
            type: "text",
            text:
              "This editor has been wired up to render code blocks as instances of the "
          },
          {
            type: "text",
            marks: [
              {
                type: "link",
                attrs: {
                  href: "http://codemirror.net",
                  title: null
                }
              }
            ],
            text: "CodeMirror"
          },
          {
            type: "text",
            text:
              " code editor, which provides syntax highlighting, auto-indentation, and similar."
          }
        ]
      },
      {
        type: "code_block",
        content: [
          {
            type: "text",
            text: "function max(a, b) {\n  return a > b ? a : b\n}"
          }
        ]
      },
      {
        type: "paragraph",
        content: [
          {
            type: "text",
            text:
              "The content of the code editor is kept in sync with the content of the code block in the rich text editor, so that it is as if you're directly editing the outer document, using a more convenient interface."
          }
        ]
      }
    ]
  };

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private location: Location,
    private dropdownService: DropDownService,
    private productService: ProductService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      name: [null, Validators.compose([Validators.required])],
      product_info: [null],
      product_detail: [this.doc],
      price: [null, Validators.compose([Validators.required])],
      category_id: [null, Validators.compose([Validators.required])],
      brand_id: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {
    this.getCategoryList();
  }
  getCategoryList() {
    this.loadingState = true;
    this.dropdownService.getCategoryDrp().subscribe((res) => {
      this.loadingState = false;
      if (res.success) {
        this.categoryList = res.data ? res.data : [];
      }
    });
  }

  getBrandList(front) {
    this.loadingState = true;
    const params: any = {};
    if (this.addForm.value.category_id) {
      if (front) { this.addForm.patchValue({ brand_id: '' }); }
      params.category_id = this.addForm.value.category_id;
    }
    this.dropdownService.getBrandDrp(params).subscribe((res) => {
      this.loadingState = false;
      if (res.success) {
        this.brandList = res.data ? res.data : [];
      }
    });
  }

  editObject() {
    this.loadingState = true;
    this.productService.getProductById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            name: response.data.name,
            price: response.data.price,
            category_id: JSON.parse(response.data.category_id).value,
            brand_id: JSON.parse(response.data.brand_id).value,
            product_info: response.data.product_info,
            product_detail: response.data.product_detail ? JSON.parse(response.data.product_detail) : ''
          });
        this.getBrandList(false);
      }
    }, (error) => {
      this.loadingState = false;
      // this.router.navigateByUrl('/product');
      this.back();
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    console.log(formdata.value.product_detail);
    console.log("formData", formdata.value);

    if (this.addForm.valid) {
      this.showLoader = true;
      const formData = new FormData();
      formData.append('name', formdata.value.name);
      formData.append('price', formdata.value.price);
      formData.append('category_id', formdata.value.category_id);
      formData.append('brand_id', formdata.value.brand_id);
      formData.append('product_detail', JSON.stringify(formdata.value.product_detail));
      formData.append('product_info', formdata.value.product_info);
      if (this.selectedFile) {
        formData.append('file', this.selectedFile);
      }
      console.log("formData", formData);

      if (this.isEditing) {
        this.productService.editProduct(this.editId, formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            // this.router.navigateByUrl('/product');
            this.back();
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }

            });
          }
        });
      } else {
        this.productService.addProduct(formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            // this.router.navigateByUrl('/product');
            this.back();
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }

            });
          }
        });
      }
    }

  }



  back() {
    this.location.back();
  }
}
